@import url(https://fonts.googleapis.com/css2?family=Recursive&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@200&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{line-height:1.4;font-family:"Recursive",sans-serif;background-color:#f5f6f7}.header-container{background-color:#fff}.header{padding:1rem;max-width:1100px;margin:auto;display:flex;align-items:center;justify-content:space-between}.header-text{font-size:1.5rem;color:#9cd4f4}.header-text:hover{color:#1996de}.header-nav-text{padding:1rem;text-decoration:none;color:#9cd4f4;border-radius:5px}.header-nav-text:nth-child(2){margin-left:5px}.header-nav-text:hover{color:#fff;background-color:#1996de}.statsbox{background-color:#5180bd;padding:1.5rem;display:flex;align-items:center;justify-content:center;color:#fff}.wpm,.seconds,.accuracy{padding:1rem}.wpm-value,.seconds-value,.accuracy-value{font-family:"Inconsolata",monospace;font-size:5rem;font-weight:lighter;text-align:center}.wpm-text,.seconds-text,.accuracy-text{text-align:center}.textbox{background-color:#fff;padding:1.5rem;overflow-wrap:unset;white-space:nowrap;overflow-x:hidden}.text{transform:translate(50%)}.text-element:nth-child(1){color:#9cd4f4}.input-container{max-width:300px;margin:auto;margin-top:1.5rem}.input-text{padding:.25rem 1rem;width:100%;border-radius:5px;border:1px solid #dfe1e6}.input-text:focus{outline:none;border:1px solid #9cd4f4}.result-overlay{position:absolute;top:0;left:0;height:100vh;width:100%;background-color:rgba(0,0,0,.7);z-index:1;padding:1rem}.result{background-color:#fff;max-width:500px;margin:auto;padding:1rem;border-radius:5px;margin-top:30vh}.result-h2{font-size:3rem}.result-h3{font-size:2rem}.result-h3-span{color:#9cd4f4}.retry{padding:.5rem;margin-top:1rem;background-color:#9cd4f4;border-radius:5px;text-align:center;color:#fff;text-decoration:none;display:block}.retry:hover{background-color:#1996de}.error{padding:1rem;margin:auto}.home{padding:1rem}.find-button,.find-status{font-size:1.5rem;text-align:center;max-width:500px;margin:auto;background-color:#5180bd;color:#fff;padding:1rem;border-radius:5px;margin-top:10vh}.find-button{cursor:pointer}.find-button:hover{opacity:.9}.matched-counter{text-align:center;font-size:3rem}.win-text,.lose-text{font-size:3rem;text-align:center;max-width:1100px;margin:auto}.win-text{color:green}.lose-text{color:red}.word-box{max-width:1100px;word-wrap:break-word;margin:auto;margin-top:10vh;font-size:1.5rem}.inline-p{display:inline}.active-p{color:#9cd4f4}.input-box{display:block;width:100%;max-width:1100px;margin:auto;margin-top:3rem;padding:.5rem;border:1px solid #dfe1e6;border-radius:5px}.input-box:focus{outline:none;border:1px solid #9cd4f4}.progress{margin-top:3rem}.user-slider,.op-slider{display:block;width:100%;max-width:1100px;margin:auto;margin-top:1.5rem;-webkit-appearance:none;-moz-appearance:none;appearance:none;background:#d3d3d3;outline:none;opacity:.7;transition:opacity .2s}.user-slider::-webkit-slider-thumb,.op-slider::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;width:25px;height:25px;cursor:pointer}.user-slider::-webkit-slider-thumb{background-color:green}.op-slider::-webkit-slider-thumb{background-color:red}.title{display:block;max-width:1100px;margin:auto;font-size:2rem;margin-bottom:1rem}
